import React, { useState, useRef, useEffect } from 'react';
import '../styles/copilot.css';

const mockAIResponses = {
  "": `
 ****  
- 3  
- 82/100  

 ****  
- ERP  
- 1410`,

  "": `  
1.   
2.   
3. v2`,

  "": `  
 ****(65%)(25%)  
 ****  
 ****`,

  "": `  
 ""  
 ""  
 ""  
 `
};

export default function AICopilotPanel({ dealData }) {
  const [messages, setMessages] = useState([{
    role: 'ai',
    content: '\n ""\n ""\n ""'
  }]);
  const [isExpanded, setIsExpanded] = useState(true);
  const inputRef = useRef();
  const messagesEndRef = useRef(null);

  // 
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const handleSend = () => {
    const question = inputRef.current.value.trim();
    if (!question) return;

    setMessages(prev => [...prev, { role: 'user', content: question }]);
    inputRef.current.value = '';
    
    setTimeout(() => {
      setMessages(prev => [...prev, { 
        role: 'ai', 
        content: generateAIResponse(question) 
      }]);
    }, 500);
  };

  const generateAIResponse = (question) => {
    const lowerQuestion = question.toLowerCase();
    
    if (lowerQuestion.includes('')) {
      return mockAIResponses[""];
    } else if (lowerQuestion.includes('') || lowerQuestion.includes('')) {
      return mockAIResponses[""];
    } else if (lowerQuestion.includes('')) {
      return mockAIResponses[""];
    } else {
      return `${mockAIResponses[""]}\n\n"${question}"`;
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSend();
    }
  };

  return (
    <>
      {/*  -  */}
      {!isExpanded && (
        <div 
          className="floating-toggle"
          onClick={() => setIsExpanded(true)}
        >
          <span className="material-icons">smart_toy</span>
        </div>
      )}

      {/*  */}
      <div 
        className={`copilot-panel ${isExpanded ? 'expanded' : 'collapsed'}`}
      >
        {/*  */}
        <div className="panel-header">
          <div className="panel-header-left">
            <span className="material-icons">smart_toy</span>
            <h3>AI </h3>
          </div>
          <button 
            className="panel-close-btn"
            onClick={() => setIsExpanded(false)}
            aria-label="AI"
          >
            <span className="material-icons">close</span>
          </button>
        </div>
        
        {/*  */}
        {isExpanded && (
          <>
            <div className="message-container">
              {messages.map((msg, i) => (
                <div key={i} className={`message ${msg.role}`}>
                  <div className="avatar">
                    {msg.role === 'ai' ? (
                      <span className="material-icons">smart_toy</span>
                    ) : (
                      <span className="material-icons">person</span>
                    )}
                  </div>
                  <div className="content">
                    {msg.content.split('\n').map((line, j) => (
                      <p key={j}>{line}</p>
                    ))}
                  </div>
                </div>
              ))}
              <div ref={messagesEndRef} />
            </div>

            <div className="input-area">
              <input
                ref={inputRef}
                type="text"
                placeholder=""
                onKeyPress={handleKeyPress}
                aria-label="AI"
              />
              <button onClick={handleSend}>
                <span className="material-icons">send</span>
              </button>
            </div>
          </>
        )}
      </div>
    </>
  );
}